@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;1,100;1,200;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
    --primary-color: #9c27b9;
    --secondary-color: #e3e6f1;
}

body{
    background-color: #e3e6f1;
    background-color: var(--secondary-color);
}

.style_loginContainer__3XoOz{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    background-image: linear-gradient(to top left, #9c27b9 50%, #e3e6f1 0);
    background-image: linear-gradient(to top left, var(--primary-color) 50%, var(--secondary-color) 0);
}

.style_signupContainer__3UumU{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    background-image: linear-gradient(to top right, #9c27b9 50%, #e3e6f1 0);
    background-image: linear-gradient(to top right, var(--primary-color) 50%, var(--secondary-color) 0);
}

.style_cardHeader__-BBnj{
    background-color: #9c27b9;
    background-color: var(--primary-color);
    color: #ffff;
    text-align: center;
    margin-bottom: 16px;
}

.style_container__1Gsd8{
    padding: 32px 16px;
    height: calc(100vh - 64px);
    box-sizing: border-box;
}

.style_addBtn__3UJ2J{
    position: 'absolute';
    bottom: 16px;
    right: 16px;
}
